  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border: 1px solid #e6eaee;
  border-radius: 4px;
  background-color: #fff;
  padding: 20px; }
  .login__head {
    text-align: center; }
  .login form p {
    position: relative;
    margin-bottom: 10px; }
