@-webkit-keyframes line-mid {
  0% {
    width: 100%; }
  50% {
    width: 0; }
  100% {
    width: 100%; } }

.hamberger {
  position: absolute;
  top: 5px;
  right: 0;
  padding: 10px;
  width: 45px;
  height: 45px;
  cursor: pointer; }
  .hamberger.disable {
    pointer-events: none; }
  .hamberger .line {
    width: 100%;
    height: 2px;
    border-radius: 2px;
    background: #909AB6;
    margin-bottom: 5px;
    transition: .5s; }
  .hamberger .line-top {
    width: 40%;
    position: relative;
    top: 4px;
    left: -1px;
    -webkit-transform: rotateZ(-45deg);
    -moz-transform: rotateZ(-45deg);
    -ms-transform: rotateZ(-45deg);
    -o-transform: rotateZ(-45deg);
    transform: rotateZ(-45deg); }
  .hamberger .line-botton {
    width: 40%;
    position: relative;
    top: -4px;
    left: -1px;
    -webkit-transform: rotateZ(45deg);
    -moz-transform: rotateZ(45deg);
    -ms-transform: rotateZ(45deg);
    -o-transform: rotateZ(45deg);
    transform: rotateZ(45deg); }
  .hamberger:hover .line {
    background: #ffffff;
    transition: .3s; }

.active {
  transition: .5s; }
  .active .line-top {
    width: 40%;
    position: relative;
    top: 4px;
    left: auto;
    left: 18px;
    -webkit-transform: rotateZ(45deg);
    -moz-transform: rotateZ(45deg);
    -ms-transform: rotateZ(45deg);
    -o-transform: rotateZ(45deg);
    transform: rotateZ(45deg); }
  .active .line-mid {
    -webkit-animation-name: line-mid;
    animation-name: line-mid;
    -webkit-animation-duration: 0.6s;
    animation-duration: 0.6s; }
  .active .line-botton {
    width: 40%;
    position: relative;
    top: -4px;
    left: auto;
    left: 18px;
    -webkit-transform: rotateZ(-45deg);
    -moz-transform: rotateZ(-45deg);
    -ms-transform: rotateZ(-45deg);
    -o-transform: rotateZ(-45deg);
    transform: rotateZ(-45deg); }

